<template>
  <div  class="nav">
    <div class="full-width">
      <div class="container">
        <ul class="nav-box">
          <li><a @click="$router.push({path:'/',query:{id:storage.getStorage('city').id}})">首页</a></li>
          <li><a @click="$router.push({path:'/services',query:{id:storage.getStorage('city')}.id})" :class="{active:$route.path === '/services' || $route.name === 'detail' }">全部服务</a></li>
          <li><a @click="$router.push({path:'/business',query:{id:storage.getStorage('city')}.id})">招商中心</a></li>
          <li><a >行业动态</a></li>
          <li><a >解决方案</a></li>
          <li><a @click="$router.push({path:'/about',query:{id:storage.getStorage('city')}.id})">关于我们</a></li>
        </ul>
        <div class="phone">
          <span>400-678-8616</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
    export default {
        name: "navigation"
    }
</script>
<style scoped>
  .nav .full-width{
    border-bottom: #ccc 1px solid;
  }
  .active{
    color: #038aeb;
  }
</style>



